/* SignUp.css */

.signup-container {
  display: flex;
  flex-direction: column;
  align-items: center; /* Center items horizontally */
  max-width: 360px; /* Reduced maximum width */
  margin: auto;
  padding: 20px; /* Adjust padding if necessary */
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.signup-form {
  width: 100%;
}

.signup-form .form-group {
  margin-bottom: 12px; /* Reduced margin-bottom for a tighter form */
}

.signup-form label {
  display: block;
  font-size: 12px; /* Smaller font size for labels */
  margin-bottom: 4px; /* Margin between label and input field */
  color: #333;
}

.signup-form input {
  width: 100%;
  max-width: 300px; /* Optional: Limit the maximum width */
  padding: 6px; /* Reduced padding for a smaller input field */
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 12px; /* Smaller font size for inputs */
  height: 32px; /* Reduced height for input fields */
}

.signup-button {
  width: 100%;
  max-width: 300px; /* Optional: Limit the maximum width */
  padding: 8px; /* Reduced padding for the button */
  border: none;
  border-radius: 4px;
  background-color: #4285f4;
  color: white;
  font-size: 14px; /* Slightly smaller font size for the button */
  cursor: pointer;
  margin-top: 20px; /* Add margin to separate from the last input */
}

.signup-button:hover {
  background-color: #357ae8;
}
